@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500&display=swap);
body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  font-size: 16px;
  font-weight: 400;
  min-height: 80vh;
}

.start-btn {
  padding: .6rem 1.2rem;
  background-color: #008376;
  color: white;
  border-radius: 4px;
  border: none;
  font-weight: 500;
  font-size: 1.2rem;
  letter-spacing: 1px;
  cursor: pointer;
  margin-top: 200px;
  font-family: 'Montserrat', sans-serif;
}

.start-btn:hover {
  background-color: #4DB6AC;
}

.navbar {
  margin: auto;
  padding: 3rem;
  font-size: 2.3rem;
  color: white;
  font-weight: 500;
  background-color: #008376;
}

#cards {
  padding-top: 3rem;
  width: 800px;
  height: 80%;
  margin: auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.card {
  
  flex-basis: 21%;
  width: 160px;
  height: 160px;
  margin-bottom: 20px;
}
.card:not(:nth-child(4n)) {
  margin-right: 20px;
}

.c {
  position: absolute;
  max-width: 160px;
  max-height: 160px;
  width: 50ch;
  height: 50ch;
  cursor: pointer;
  will-change: transform, opacity;
}

.front,
.back {
  background-size: cover; 
}

.back {
  background-image: url(https://source.unsplash.com/trYl7JYATH0);
}

